import { toSVG } from '@carbon/icon-helpers';
export const getSvg = (iconPath, customAttrs) => {
    return toSVG({
        ...iconPath,
        attrs: {
            ...iconPath.attrs,
            ...customAttrs,
            'focusable': false,
            'preserveAspectRatio': 'xMidYMid meet',
            'aria-hidden': true,
        },
    });
};
